function setAttribute(dom, key, value) {
  // å½“å±žæ€§åä¸ºclassNameæ—¶,éœ€è½¬ä¸ºclass
  if (key === 'className') key = 'class'
  // å¦‚æžœå±žæ€§æ˜¯onXXXçš„å½¢å¼,åˆ™è¯´æ˜Žæ˜¯äº‹ä»¶ç›‘å¬
  if (/^on\w+/.test(key)) {
    dom[key.toLowerCase()] = value || ''
  } else if (key === 'style') {
    if (value && typeof value === 'string') {
      dom.style.cssText = value
    } else if (value && typeof value === 'object') {
      for (let name in value) {
        dom.style[name] = value[name]
        // typeof value[name] === 'number' ? value[name] + 'px' : value[name]
      }
    }
    // å¦‚æžœæ˜¯æ™®é€šå±žæ€§,åˆ™ç›´æŽ¥æ›´æ–°
  } else {
    if (key in dom) {
      dom[key] = value || ''
    }
    if (value) {
      dom.setAttribute(key, value)
    } else {
      dom.removeAttribute(key, jvalue)
    }
  }
}

// ReactDom.render
function render(vnode, container) {
  // å½“vnodeä¸ºå­—ç¬¦ä¸²æ—¶,æ¸²æŸ“æ–‡æœ¬èŠ‚ç‚¹
  if (typeof vnode === 'string') {
    const textNode = document.createTextNode(vnode)
    return container.appendChild(textNode)
  }
  const dom = document.createElement(vnode.tag)
  if (vnode.attr) {
    Object.keys(vnode.attr).forEach((key) => {
      const value = vnode.attr[key]
      setAttribute(dom, key, value)
    })
  }
  // éåŽ†å­èŠ‚ç‚¹
  vnode.children.forEach((child) => render(child, dom))
  return container.appendChild(dom)
}

const ReactDom = {
  render: (vnode, container) => {
    container.innerHTML = ''
    return render(vnode, container)
  }
}

export default ReactDom
